.task {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin: 10px 0;
}
.task img {
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  background-color: #404040;
  border-radius: 50%;
  margin: 5px 5px 0 0;
}
.task .task-inner {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.task .task-inner .task-priority {
  width: 5px;
  height: 100%;
  background-color: red;
}
.task .task-inner p {
  height: auto;
  box-sizing: border-box;
  width: 100%;
  padding: 10px 5px;
  text-wrap: normal;
  text-align: start;
  margin: 0;
  background: var(--tg-theme-secondary-bg-color, #000);
}
.task .task-inner .task-check-box {
  box-sizing: border-box;
  width: 20px;
  height: 100%;
  background: linear-gradient(180deg, #D7B258, #E57770);
  border-left: 0;
  border-radius: 0;
  margin-left: 10px;
}
.task .task-buttons {
  margin-top: 10px;
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.task .task-buttons button {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  color: white;
  border: 0;
  border-radius: 15px;
  height: 25px;
  padding: 0;
  box-sizing: border-box;
  flex-basis: 25%;
}
.task .task-buttons button:first-child {
  background: #79C063;
}
.task .task-buttons button:nth-child(2) {
  background: #D7B258;
}
.task .task-buttons button:last-child {
  background: #E57770;
}

